import React from 'react';
import MenuButtons from './menu/MenuButtons.jsx';
import DifficultyButtons from './menu/DifficultyButtons.jsx';
import Instructions from './menu/Instructions.jsx';

import generateColours from '../utils/generateColours';

export default class Menu extends React.Component {
	constructor() {
		super();

		this.state = {
			colours: generateColours.game()
		}
	}
	render() {
		let style = { color: this.state.colours.primary };
		return (
			<div className="menu">
				<div className="container">
					<Instructions colours={this.state.colours} />
					<div style={style} className="animated bounceInDown">
						<h1>Words With Enemies</h1>
						<p>Expand your vocabulary whilst boosting your ego!</p>
					</div>
					<MenuButtons colours={this.state.colours} />
					<DifficultyButtons colours={this.state.colours} />
				</div>
			</div>
		);
	}
}



