var React = require('react');
var Letter = require('./Letter.jsx');

var UserWord = React.createClass({
	getInitialState: () => {
		return {
			letters: []
		};
	},

	removeFromWord: letterToRemove => {
		var letters = this.state.letters;
		for (let letter of letters) {
			if (letterToRemove.id === letter.id) {
				letters.splice(letters.indexOf(letter), 1);
			}
		}
	},

	addToWord: letter => {
		// this.state.letters.push({id: 1, value: 'a'})
		var letters = this.state.letters;
		letters.push({ id: letter.id, value: letter.value });
		this.setState({ letters: letters });
	},

	render: function() {
		var letters = this.state.letters.map(letter => {
			return ( <Letter remove={this.removeFromWord} value={letter.value} key={letter.id} /> )
		});

		var wordStyle = {
			backgroundColor: this.props.colours.userSecondary,
			borderColor: this.props.colours.userTertiary
		};

		var messageStyle = { color: this.props.colours.userTertiary };
		var message = this.state.letters.length === 0 ? <div className="formedWordMessage" style={messageStyle}>Make a word with the letters!</div> : '';

		var podge = this.addToWord.bind({ id: 1, value: 'a' });

		return (
			<div onClick={podge} className="formedWord wordRotate" id="userWord" style={wordStyle}>
				{message}
				<ul>{letters}</ul>
			</div>
		);
	}

});

module.exports = UserWord;


